<template>
  <el-menu :default-active="thishash"
           class="el-menu-vertical-demo"
           background-color="#000"
           text-color="#fff"
           active-text-color="#ccccd6"
           router
           unique-opened>
    <div id="Tit_div">
      <span class="el-icon-user-solid"></span>
      外卖管理中心
    </div>
    <div v-for="(item,index) in powerarr"
         :key="index">
      <el-menu-item :index="item.url"
                    v-if="!item.children">
        <i :class="item.icon"></i>
        <span slot="title">{{item.name}}</span>
      </el-menu-item>
      <el-submenu :index="item.url"
                  v-else>
        <template slot="title">
          <i :class="item.icon"></i>
          <span>{{item.name}}</span>
        </template>
        <el-menu-item-group>
          <el-menu-item :index="child.url"
                        v-for="(child,index) in item.children"
                        :key="index">{{child.name}}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </div>
  </el-menu>
</template>

<script>
export default {
  computed: {
    powerarr () {
      let newarr = this.list.filter((item) => {
        return item.roles.includes(localStorage.role);
      });
      return newarr;
    }
  },
  data () {
    return {
      thishash: '',
      role: '',
      list: [
        { url: "/index/Home", icon: "el-icon-s-home", name: "后台首页", roles: ["super", "normal"] },
        { url: "/index/Order", icon: "el-icon-document", name: "订单管理", roles: ["super", "normal"] },
        {
          url: "/index/orderman", icon: "el-icon-s-goods", name: "商品管理", children: [
            { url: "/index/Orderpage", name: "商品列表" },
            { url: "/index/Orderadd", name: "商品添加" },
            { url: "/index/Ordercategory", name: "商品分类" }
          ], roles: ["super", "normal"]
        },
        { url: "/index/Store", icon: "el-icon-s-shop", name: "店铺管理", roles: ["super"] },
        {
          url: "/index/accman", icon: "el-icon-s-custom", name: "账号管理", children: [
            { url: "/index/Accountlist", name: "账号列表" },
            { url: "/index/Accountadd", name: "账号添加" },
            { url: "/index/Changepwd", name: "修改密码" }
          ], roles: ["super"]
        },
        {
          url: "/index/info", icon: "el-icon-s-data", name: "销售统计", children: [
            { url: "/index/Orderinfo", name: "商品统计" },
            { url: "/index/Indetinfo", name: "订单统计" }
          ], roles: ["super"]
        },
      ]

    }
  },
  created () {
    //取得role  
    this.thishash = this.$route.path
    this.role = localStorage.getItem('role')
  },
}
</script>

<style lang="less" scoped>
.el-menu {
  border-right-width: 0;
  background-color: #000000;
}
