html,
body {
  height: 100%;
  // font-family: HYSSEJ !important;
}
#Tit_div {
  color: #fff;
  font-size: 18px;
  text-align: center;
  line-height: 40px;
  padding: 10px 20px 10px 10px;
}
</style>