<template>
  <el-container>
    <el-aside width="200px">
      <Nav />
    </el-aside>
    <el-container>
      <el-header style="height:80px">
        <!-- 头部 -->
        <el-breadcrumb separator-class="el-icon-arrow-right"
                       style="font-size: 16px;padding:20px 0">
          <el-breadcrumb-item v-for="item in breadlist"
                              :key="item">{{item}}</el-breadcrumb-item>
        </el-breadcrumb>
        <div id="customer">
          <router-link to='/index/Accountinfo'>
            <span>{{acc}}</span>
            <!-- 头像 -->
            <img :src="imgUrl">
          </router-link>

        </div>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import Nav from './Nav'
import { checktoken, accountinfo } from '@/API/apis'

export default {
  components: {
    Nav
  },
  data () {
    return {
      id: "",
      acc: '',
      imgUrl: "",
      breadlist: [],
    }
  },
  methods: {
    // 获取账号信息
    refreshinfo () {
      this.id = localStorage.getItem("id")
      accountinfo(this.id).then(res => {
        this.imgUrl = res.data.accountInfo.imgUrl
      });
    }
  },

  created () {
    this.refreshinfo()
    this.token = localStorage.getItem('token')
    checktoken(localStorage.getItem('token')).then(res => {
      if (res.data.code !== 0) {
        this.acc = "请登录"
      } else {
        //取得用户名
        this.acc = localStorage.getItem('acc')
        // 首次监视hash的变化
        this.breadlist = this.$route.meta.breadlist
        //取得bus事件的传值
        this.$bus.$on("Userheader", () => {
          this.refreshinfo();
        })
      }
    })
  },

  //监视hash的变化
  watch: {
    $route (to) {
      this.breadlist = to.meta.breadlist
    }
  }
}
</script>

<style lang="less" scoped>
.el-aside {
  display: block;
  background-color: #000000;
  height: 1000px;
}
.el-main {
  height: 900px;
  overflow: auto;
}
#customer {
  display: inline-block;
  a {
    color: #808080;
    text-decoration: none;
  }
}
.el-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: url(../assets/imgs/08.png) no-repeat;
  background-position-x: 100px;
  #customer {
    font-size: 16px;
    line-height: 60px;
    color: #808080;
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      vertical-align: middle;
      margin-left: 15px;
    }
  }
}
</style>